import { defineComponent, computed, ref } from 'vue';
import { useStore } from '@/store';
import { useRouter, useRoute } from 'vue-router';
import { pathMapToMenu } from '@/utils/map-menus';
export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },

  setup() {
    //store
    const store = useStore();
    const userMenus = computed(() => store.state.login.userMenus); //router

    const router = useRouter();
    const route = useRoute();
    const currentPath = route.path; //data

    const menu = pathMapToMenu(userMenus.value, currentPath);
    const defaultValue = ref(menu.id + ''); //event handle

    const handleMenuItemClick = item => {
      console.log('--------'); // console.log(item.url)

      router.push({
        path: item.url ?? '/not-found' // path: '/not-found'

      });
    };

    return {
      userMenus,
      currentPath,
      defaultValue,
      handleMenuItemClick
    };
  }

});